export default({
	 
	data:function(){
		return {
			pageLoad:false,
			pageData:[],
			content:""
		}
	},
	created:function(){
		this.getPage();
	},
	methods:{
		getPage:function(){
			var that=this;
			that.app.get({
				url:that.app.apiHost+"/index.php?m=kefu&a=data&ajax=1",
				
				dataType:"json",
				success:function(res){
					that.pageLoad=true;
					that.pageData=res.data
					console.log(res.data);
				}
			})
		},
		submit:function(){
			var that=this;
			that.app.post({
				url:that.app.apiHost+"/index.php?m=kefu&a=save&ajax=1",
				data:{
					content:that.content
				},
				success:function(res){
					that.content="";
					that.getPage();
				}
			})
			
		}
	}
})
